            {/* <button className="bg-yellow-500 text-white m-2 py-2 px- md:px-10 rounded-2xl hover:bg-white hover:text-yellow-500">Choose File</button>
            
            <button className="bg-yellow-500 text-white m-2 py-2 px-6 md:px-10 rounded-2xl hover:bg-white hover:text-yellow-500">Upload</button> */}
