import React from 'react';
import { Route, Switch } from 'react-router-dom';

import Login from './Login';
import Welcome from './Dashboard';
import Customer from './Customer';
import Products from './Products';
import Testing from './Testing';
import Reports from './Reports';
import Productedit from './Productedit'
import Customersedit from './Customeredit';
import TestingView from './Testingview';
import Testingedit from './Testingedit';
import TestingView2 from './Testingview2';
import Testingview3 from './Testingview3';
import Testingview4 from './Testingview4';
import FourZeroFour from './FourZeroFour';

const PrivateRoute = (props) => {
    const token = sessionStorage.getItem('token');
    if (token) {
        return <Route exact={true} path={props.path} component={props.component} />
        console.log(`Component ${props.component}`)
    } else {
        return <Login {...props} />
    }
}


const Router = (props) => {
    return <Switch>

        <Route path="/" component={Login} exact={true} />

        <Route path="/login" component={Login} />
        <Route path="/testingview/:id" component={TestingView} />
        <Route path="/testingview2/:id" component={TestingView2} />
        <Route path="/testingview3/:id" component={Testingview3} />
        <Route path="/testingview4/:id" component={Testingview4} />

        <PrivateRoute path="/welcome" component={Welcome} />
        <PrivateRoute path="/products" component={Products} />
        <PrivateRoute path="/customers" component={Customer} />
        <PrivateRoute path="/testings" component={Testing} />
        <PrivateRoute path="/testingsedit/:id" component={Testingedit} />
        <PrivateRoute path="/reports" component={Reports} />
        <PrivateRoute path="/Editcategory/:id" component={Productedit} />
        <PrivateRoute path="/Editcustomer/:id" component={Customersedit} />

        <Route component={FourZeroFour} />

    
    </Switch>
}

export default Router;