import React, {useState, useEffect} from 'react';
import {useHistory} from "react-router-dom";

import {useParams} from 'react-router-dom';
import QRCode from 'react-qr-code';
import { jsPDF } from "jspdf";
import "jspdf-autotable";
import background from '../images/ba.jpg';
import t from './t.css' 

export default function Testingview3() {



const [roleau, setroleau] = useState ('');
const history = useHistory()
const [productDetail, setproductDetail] = useState([0])




const { id } = useParams("");
    console.log(id);



    const getdata = async () => {

        const res = await fetch(`https://web-production-d8bb8.up.railway.app/api/getuser/${id}`, {
            method: "GET",
            headers: {
                "Content-Type": "application/json"
            }
        });

        const data = await res.json();
        console.log(data);

   if (res.status === 422 || !data) {
  console.log("error ");

} else {
    setproductDetail(data)
   

console.log("get data");


 }
    }


    
  
    useEffect(() => {
        
        getdata();
              

    }, []);



  // Dummy data
  const dummyData = {

    certifiedBy: "NATIONAL GOLD"
  };

  const exportPdf = async () => {
    const doc = new jsPDF({ orientation: "landscape" });
  
    doc.autoTable({
      html: "#my-table",
    });
  
    doc.save("mypdf.pdf");
  };

  return (
    <>

      <div className='MainDiv'>
  
      <img  src={productDetail.imageURL} alt="image description" />

      <br />
   

                <div className='MainDiv2'>
                <h1>Certificate:</h1>
                <label htmlFor="" className='isis'>{`NG-${productDetail.repairing}`}</label>
                </div>

                
                <div className='MainDiv2'>
                <h1>Particular:</h1>
                <label htmlFor="" className='isis'>{productDetail.brand}</label>
                </div>

                <div className='MainDiv2'>
                <h1>Karat:</h1>
                <label htmlFor="" className='isis'>{`${productDetail.cost} ${productDetail.dt}`}</label>
                </div>

                <div className='MainDiv2'>
                <h1>Gold Purity:</h1>
                <label htmlFor="" className='isis'>{productDetail.cargoCost}</label>
                </div>

                <div className='MainDiv2'>
                <h1>Certified By:</h1>
                <label htmlFor="" className='isis'>{`National Gold`}</label>
                </div>

                <div className='MainDiv2'>
                <h1>Customer:</h1>
                <label htmlFor="" className='isis'>{productDetail.model}</label>
                </div>
            
 
    
      </div>
      <br />

      <div className='MainDiv3'>
              <QRCode
              size={150}
              bgColor="white"
              fgColor="black"
              value={`https://nationalgold.netlify.app/testingview3/${id}`}
              />
            </div>

         </>
  );
}