import React, {useState, useEffect} from 'react';
import Sidebar from './Sidebar';
import {useHistory} from "react-router-dom";
import { jsPDF } from "jspdf";
import "jspdf-autotable";

const Reports = (props) => {

  const history = useHistory()   
  const [productDetail, setproductDetail] = useState({
  search: "",
  model: "",
  serial: "",
  })

  const [roleau, setroleau] = useState ('');
    const [userState, setUserState] = useState([]);

    const [useremail, setuserEmail] = useState([]);


    const getdata = async () => {

      const findEmail2 = sessionStorage.getItem('user'); 
    
      const res = await fetch(`https://web-production-d8bb8.up.railway.app/api/postbyemail/${findEmail2}`, {
          method: "GET",
          headers: {
              "Content-Type": "application/json"
          }
          
      });
  
      const data = await res.json();
      console.log(data, 'data');
      setUserState(data);    
  
  }
  
  useEffect(()=>{
    const udata = sessionStorage.getItem('user');    
    setuserEmail(udata); 
    
    getdata()

}, []);


///delelte single data

const deletedata = async (id) => {


  const res2 = await fetch(`https://web-production-d8bb8.up.railway.app/api/deletepost/${id}`, {
      method: "DELETE",
      headers: {
          "Content-Type": "application/json"
      }
      
      
  });
alert("delete successfully")

  
  getdata()

}

const exportPdf = async () => {
  const doc = new jsPDF({ orientation: "landscape" });

  doc.autoTable({
    html: "#my-table",
  });

  doc.save("mypdf.pdf");
};


  return (
    
    <div className="flex flex-col md:flex-row">
      <Sidebar />

      {/* Main Content */}
      <div className="w-full md:w-3/4 p-4 flex flex-col items-center">
        <div className='bg-yellow-500 h-12 w-80 rounded-2xl text-center m-3'><h1 className="text-4xl font-bold mb-4 text-white">Reports</h1></div>

        <div className="max-w-md mx-auto bg-white shadow-md rounded-md overflow-hidden ">
          <div className="p-4 flex flex-col items-center space-y-4">
            {/* Input 1 */}
            {/* <label className="block text-sm font-medium text-gray-600 mb-1">Date:</label>
            <input
              type="date"
              className="w-full p-2 border text-center rounded-md mb-4 focus:outline-none focus:border-blue-500"
            /> */}

            {/* Button 1 */}
            {/* <button className="bg-green-500 text-white py-2 px-4 rounded-2xl hover:bg-white hover:text-green-500 hover:border b-2 border-green-500">Search</button> */}

            {/* Input 2 */}
            {/* <label className="block text-sm font-medium text-gray-600 mb-1">ID:</label>
            <input
              type="text" placeholder='Enter ID here'
              className="w-full p-2 border rounded-md mb-4 focus:outline-none focus:border-blue-500"
            /> */}

            {/* Button 2 */}
            {/* <button className="bg-green-500 text-white py-2 px-4 rounded-2xl hover:bg-white hover:text-green-500 hover:border b-2 border-green-500">Search</button> */}
          </div>
        </div>

        {/* Reports Table */}  <div style={{ padding: "30px" }}>
      <button
        className="btn btn-primary float-end mt-2 mb-2"
        onClick={exportPdf}
      >
        Export
      </button>
      </div>


        <div className="overflow-x-auto w-full">
          <table className="min-w-full bg-white border border-gray-300" id="my-table">
            <thead>
              <tr>
                <th className="py-2 px-4 border-b text-center">Date</th>
                <th className="py-2 px-4 border-b text-center">Customer</th>
                <th className="py-2 px-4 border-b text-center">Product</th>
                <th className="py-2 px-4 border-b text-center">Weight</th>
                <th className="py-2 px-4 border-b text-center">Testing Fee</th>
                <th className="py-2 px-4 border-b text-center">Karat</th>
                <th className="py-2 px-4 border-b text-center">Purity</th>
                <th className="py-2 px-4 border-b text-center">Edit</th>
                <th className="py-2 px-4 border-b text-center">Delete</th>
                <th className="py-2 px-4 border-b text-center">View</th>
               
              </tr>
            </thead>
            <tbody>
              {userState.map((item) => (
                <tr key={item.id}>
                  <td className="py-2 px-4 border-b text-center">{item.dt}</td>
                  <td className="py-2 px-4 border-b text-center">{item.model}</td>
                  <td className="py-2 px-4 border-b text-center">{item.brand}</td>
                  <td className="py-2 px-4 border-b text-center">{item.qty}</td>
                  <td className="py-2 px-4 border-b text-center">{item.year}</td>
                  <td className="py-2 px-4 border-b text-center">{item.cost}</td>
                  <td className="py-2 px-4 border-b text-center">{item.cargoCost}</td>
                  <td className="py-2 px-4 border-b text-center">
                    <button type='button'>
                    <a href={`testingsedit/${item._id}`} class="font-medium text-blue-600 dark:text-blue-500 hover:underline">Edit</a>
                      </button>
                  </td>
                  <td className="py-2 px-4 border-b text-center">
                  <button type='button'>
                    <a href={`testingview/${item._id}`} class="font-medium text-blue-600 dark:text-blue-500 hover:underline">Design 1</a>
                      </button>
                      <br />
                      <button type='button'>
                    <a href={`testingview2/${item._id}`} class="font-medium text-blue-600 dark:text-blue-500 hover:underline">Design 2</a>
                      </button>

                      <br />
                      <button type='button'>
                    <a href={`testingview3/${item._id}`} class="font-medium text-blue-600 dark:text-blue-500 hover:underline">QR Design</a>
                      </button>

                      <br />
                      <button type='button'>
                    <a href={`testingview4/${item._id}`} class="font-medium text-blue-600 dark:text-blue-500 hover:underline">Print Design</a>
                      </button>


                  </td>

                  <td className="py-2 px-4 border-b text-center">
                    <button className="bg-red-500 text-white py-2 px-1 rounded-2xl hover:bg-white hover:text-red-500 hover:border b-2 border-red-500" onClick={() => deletedata(item._id)}>Delete</button>
                  </td>
            
              
                </tr>
              ))}
            </tbody>
          </table>
        </div>
      </div>
    </div>
  );
};

export default Reports;
