// export default TestingView;

import React, {useState, useEffect} from 'react';
import {useHistory} from "react-router-dom";
import {useParams} from 'react-router-dom';
import yourImage from '../images/certificate_front.png';
import QRCode from 'react-qr-code';
import { jsPDF } from "jspdf";
import "jspdf-autotable";

export default function Testingview() {



const [roleau, setroleau] = useState ('');
const history = useHistory()
const [productDetail, setproductDetail] = useState([0])




const { id } = useParams("");
    console.log(id);



    const getdata = async () => {

        const res = await fetch(`https://web-production-d8bb8.up.railway.app/api/getuser/${id}`, {
            method: "GET",
            headers: {
                "Content-Type": "application/json"
            }
        });

        const data = await res.json();
        console.log(data);

   if (res.status === 422 || !data) {
  console.log("error ");

} else {
    setproductDetail(data)
   

console.log("get data");


 }
    }


    
  
    useEffect(() => {
        
        getdata();
              

    }, []);



  // Dummy data
  const dummyData = {

    certifiedBy: "NATIONAL GOLD"
  };

  const exportPdf = async () => {
    const doc = new jsPDF({ orientation: "landscape" });
  
    doc.autoTable({
      html: "#my-table",
    });
  
    doc.save("mypdf.pdf");
  };

  return (
    <>
      <div className='flex justify-between' id="my-table">
        {/* <Sidebar /> */}
  
        <div className='border-2 border-solid border-yellow-600 w-[40vw] m-auto p-2 h-[45vh] grid items-center rounded-lg bg-yellow-200' >
      
          <h1 className='text-center text-3xl font-bold text-yellow-600'>Certificate Of Authenticity</h1>
          <h1 className='text-center text-3xl font-bold text-yellow-600'>{productDetail.model}</h1>
          <div className='flex justify-between mt-5 items-center'>
            <div className='w-32 h-32'>
            <img  src={productDetail.imageURL} alt="image description" />
            </div>
            <div className='grid justify-center  p-5'>
              <div className='flex justify-between w-[15vw]'>
                <h1 className='font-bold text-yellow-600'>Certificate</h1>
                <label htmlFor="" className=''>{`NG-${productDetail.repairing}`}</label>
              </div>
              <div className='flex justify-between w-[15vw]'>
                <h1 className='font-bold text-yellow-600'>Particular</h1>
                <label htmlFor="" className=''>{productDetail.brand}</label>
              </div>
              <div className='flex justify-between w-[15vw]'>
                <h1 className='font-bold text-yellow-600'>Karat</h1>
                <label htmlFor="" className=''>{`${productDetail.cost} ${productDetail.dt}`}</label>
              </div>
              <div className='flex justify-between w-[15vw]'>
                <h1 className='font-bold text-yellow-600'>Gold Purity</h1>
                <label htmlFor="" className=''>{productDetail.cargoCost}</label>
              </div>
              <div className='flex justify-between w-[15vw]'>
                <h1 className='font-bold text-yellow-600'>Certified </h1>
                <label htmlFor="" className=''>{dummyData.certifiedBy}</label>
              </div>
            </div>
            <div className='w-32 h-32'>
              <QRCode
              size={120}
              bgColor="white"
              fgColor="black"
              value={`https://nationalgold.netlify.app/testingview3/${id}`}
              />
            </div>
          </div>
          {/* <div className='flex flex-row mt-5 w-[20vw] justify-around m-auto'>
            <button className='bg-yellow-500 rounded-full px-4 py-1 ms-5 hover:bg-white text-white hover:text-yellow-500' onClick={exportPdf}>Download PDF</button>
            <button className='bg-yellow-500 rounded-full px-4 py-1 hover:bg-white text-white hover:text-yellow-500'>GET QR CODE</button>
          </div> */}
        </div>
      </div>
     
    </>
  );
}